.projectOuter {
  width: 33%;
  padding: 0;
  margin-top: 1rem;
  height: 232px;
  /* max 4 lines */ }

.project {
  padding: 16px 1rem 16px 1rem;
  border: 0 solid #7d97ad;
  -ms-flex-direction: column;
      flex-direction: column;
  /* https://www.w3schools.com/howto/howto_css_cards.asp */
  display: -ms-flexbox;
  display: flex;
  border-radius: 2px;
  -webkit-transition: -webkit-box-shadow 0.25s;
  transition: -webkit-box-shadow 0.25s;
  -o-transition: box-shadow 0.25s;
  transition: box-shadow 0.25s;
  transition: box-shadow 0.25s, -webkit-box-shadow 0.25s;
  -webkit-box-shadow: 0 1px 1px 0 rgba(0, 0, 0, 0.14), 0 1px 3px 0 rgba(0, 0, 0, 0.12), 0 2px 1px -2px rgba(0, 0, 0, 0.2);
          box-shadow: 0 1px 1px 0 rgba(0, 0, 0, 0.14), 0 1px 3px 0 rgba(0, 0, 0, 0.12), 0 2px 1px -2px rgba(0, 0, 0, 0.2);
  margin: 0 0.4rem 0 0.4rem;
  height: 232px; }

.project:hover {
  /*box-shadow: 0 2px 4px 0 rgba(0,0,0,0.2);*/
  /*opacity: 1.0;*/
  -webkit-transition-delay: 0.2s;
       -o-transition-delay: 0.2s;
          transition-delay: 0.2s; }

.projectTitleSmall, .projectTitle {
  min-height: 2rem;
  height: auto;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row; }

.projectTitle {
  font-size: 1.5rem; }

.projectTitleSmall {
  font-size: 1.3rem; }

.projectName {
  margin-right: 1rem; }

.projectDate {
  font-size: 0.8rem;
  display: inline-block;
  opacity: 0.85;
  /* default for non title */
  margin-top: 8px; }

.projectDesc {
  margin-top: 0.5rem;
  -ms-flex-positive: 100;
      flex-grow: 100;
  opacity: 0.65;
  font-size: 0.8rem;
  /*padding: 0.3rem;*/
  /*border-radius: 1rem;*/
  /*border: 1px dotted;*/ }

.projectDesc:hover {
  cursor: pointer;
  opacity: 0.8;
  /* barely visible change */ }

.projectTags {
  margin-top: 1rem;
  margin-bottom: 0.5rem; }

.projectLinks {
  margin-left: 1rem; }

/** Media queries **/
@media all and (max-width: 1200px) {
  .project {
    margin: 0 0.35rem 0 0.35rem; } }

@media all and (max-width: 1155px) {
  .projectTitle {
    font-size: 1.3rem; }
  .projectTitleSmall {
    font-size: 1.1rem; } }

@media all and (max-width: 1000px) {
  .projectOuter {
    width: 50%; }
  .project {
    margin: 0 0.5rem 0 0.5rem; } }

@media all and (max-width: 820px) {
  .projectOuter {
    height: 242px; }
  .project {
    height: 210px; }
  .projectTitle, .projectTitleSmall {
    min-height: unset;
    /*flex-wrap: wrap;*/ } }

@media all and (max-width: 600px) {
  .projectOuter {
    height: 282px; }
  .project {
    height: 250px;
    margin: 0 0.35rem 0 0.35rem; } }

@media all and (max-width: 550px) {
  .projectOuter {
    width: 100%;
    height: 222px; }
  .project {
    /* single col*/
    height: 190px;
    max-width: 85%;
    margin: 0 auto 0 auto; } }

@media all and (max-width: 400px) {
  .project {
    max-width: 90%; } }

@media all and (max-width: 350px) {
  .projectOuter {
    height: 232px; }
  .project {
    height: 200px; } }



